/* spinner */
#spinner{
  left: 50%;
  top: 50%;
  margin: auto;
  position: absolute;
}


/*  headers  */
.header{
  height: auto;
  background:linear-gradient(rgba(0, 168, 255, 0.3), rgba(0, 168, 255, 0.3)), no-repeat center center fixed;
  padding-top: 1rem;
  padding-bottom: 1rem;
  margin-bottom: 2rem;
  border: 2px solid #0bceaf;
  border-bottom-left-radius: 40%;
  border-top-right-radius: 40%;
}

.welcome-text{
  padding: 0.8rem;
  background-color: #ee1995;
  width: fit-content;
  border: 1px solid #ee1995;
  border-radius: 0.7rem;
  border-bottom-left-radius: 0;
  text-align: right;
  transform: skewX(-10deg);
  color: white;
  font-weight: bold;
  font-size: large;
}
.profile-img{
  padding: 0.2rem;
  border: 2px solid white;
  border-radius: 50%;
  text-align: center;
  width: 30%;
  height: 30%;
}
.text-slider-items{
  display: none;
}

/* about */

#about{
  background:linear-gradient(rgba(0, 168, 255, 0.3), rgba(0, 168, 255, 0.3)), no-repeat center center fixed;
  border: 2px #0bceaf;
  background-color: #0bceaf;
  border-bottom-left-radius: 15%;
  border-bottom-right-radius: 15%;
  padding: 1rem;
  color: ;
  margin-bottom: 2rem;
}
#about h4{
  text-align: center;
  text-decoration: underline;
  color: white;
}
.about-me{
  padding: 0.5rem;
}

.about-me p{
  font-size: 1rem;
  font-weight: 500;
  font-style: normal;
  font-family: sans-serif
}
.skills{
  padding: 00.5rem;
}
.skills ul{
  margin: 0;
  padding: 0;
  display: inline-block;
  list-style: none;
}
.skills ul li{
  display: inline-block;
  padding: 5px;
  border: 1px solid white;
  border-radius: 3px;
  margin: 6px;
  color: white;
}

/* work */
.work h4{
  font-weight:bold;
  font-size:30px
}
.education-item h5{
  color:#0bceaf;
  font-style:italic;
  font-weight:bold
}
.education-item small{
  color:#0bceaf
}


/* contact */
 #contact{
  background:linear-gradient(rgba(0, 168, 255, 0.3), rgba(0, 168, 255, 0.3)), no-repeat center center fixed;
  border: 2px #0bceaf;
  background-color: #0bceaf;
  margin-top: 2rem;
 }
.contact-form h4{
  font-size:30px;
  font-family: sans-serif;
}


/* footer */

.list{
  padding: 0;
  list-style: none;
}
.list li{
  display: inline-block;
  margin: 0px 4px 0px;
  padding: 0.5rem;
}
#footer{
  background:linear-gradient(rgba(0, 168, 255, 0.3), rgba(0, 168, 255, 0.3)), no-repeat center center fixed;
  border: 2px #0bceaf;
  background-color: #0bceaf;
}
